@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@300;600;700&display=swap");
*,
*::after,
*::before {
  box-sizing: border-box;
}

:root {
  --clr-pale-blue: hsl(223, 100%, 88%);
  --clr-red: hsl(354, 100%, 66%);
  --clr-gray: hsl(0, 0%, 59%);
  --clr-dark-blue: hsl(209, 33%, 12%);
  --clr-royal-blue: hsl(223,87%,62%);
  --fw-light: 300;
  --fw-medium: 600;
  --fw-heavy: 700;
}

body {
  font-family: "Libre Franklin", sans-serif;
  font-size: 20px;
  margin: 0;
}

img {
  max-width: 100%;
}

.container {
  width: 95%;
  margin: 0 auto;
  text-align: center;
  padding: 5em 0;
}

.logo {
  margin-bottom: 2em;
}

.page-title {
  font-size: 1.8rem;
  font-weight: var(--fw-light);
  color: var(--clr-gray);
}
.page-title span {
  color: black;
  font-weight: var(--fw-heavy);
}

.page-desc {
  font-size: 0.9rem;
}

.error-message {
  display: none;
}

.form {
  width: 90%;
  margin: 2em auto;
  display: flex;
  flex-direction: column;
}
.form .email {
  width: 100%;
  margin-bottom: 0.75em;
}
.form .email input {
  width: 100%;
  padding: 1.5em;
  border: 1px solid var(--clr-pale-blue);
  border-radius: 50px;
}
.form .email input::placeholder {
  font-size: 0.9rem;
  color: var(--clr-pale-blue);
}
.form .button-container {
  width: 100%;
}
.form button {
  width: 100%;
  background: var(--clr-royal-blue);
  color: white;
  padding: 1em;
  font-size: 0.9rem;
  border: transparent;
  border-radius: 50px;
  box-shadow: 0 5px 5px #c2d3ff, 0 5px 5px #c2d3ff;
  cursor: pointer;
}

.form.error .error-message {
  display: block;
  color: var(--clr-red);
  font-size: 0.8rem;
  font-weight: var(--fw-light);
  text-align: left;
  margin: 1em;
}
.form.error input {
  border: 1px solid var(--clr-red);
}

.page-image {
  margin: 4em 0 6em;
}

.fab {
  color: var(--clr-royal-blue);
  border: 1px solid var(--clr-pale-blue);
  border-radius: 20px;
  padding: 0.5em;
  font-size: 1rem;
  width: 2rem;
  margin: 0 0.25em;
}

.copyright {
  color: var(--clr-gray);
  font-weight: var(--fw-light);
  font-size: 0.8rem;
  margin-top: 4em;
}

@media (min-width: 600px) {
  .container {
    max-width: 570px;
  }

  .page-title {
    font-size: 2.5rem;
    letter-spacing: 1px;
  }

  .form {
    width: 100%;
    flex-direction: row;
  }

  .email {
    flex: 2;
    margin-right: 1em;
  }

  .button-container {
    flex: 1;
  }
  .button-container button:hover {
    background: rgba(74, 122, 242, 0.719);
  }

  .fab:hover {
    cursor: pointer;
    background: var(--clr-royal-blue);
    color: white;
  }

  .page-image {
    margin-top: 1.5em;
  }
}

